@font-face {
  font-family: "azosans-regular";
  src: url("../fonts/azosans-regular.woff2") format("woff2"), url("../fonts/azosans-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  background: #FFF;
  font-family: "azosans-regular", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4;
}

.container {
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  padding: 20px 0;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.54);
  background-color: #181818;
}

.header__wrap {
  display: flex;
}

.header__nav {
  display: flex;
}

.main-nav__list {
  display: flex;
  justify-content: space-between;
}

.main-nav__list--sub2 {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 340px;
  background-color: #181818;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.54);
}

.main-nav__item {
  position: relative;
  white-space: nowrap;
}

.main-nav__item:hover .main-nav__list--sub2 {
  display: block;
}

.main-nav__item--dropdown {
  color: red;
}

.main-nav__item--dropdown > .main-nav__link:after {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 8px;
  vertical-align: middle;
  border-width: 6px 4px 0;
  border-style: solid;
  border-color: #8f8f9b transparent transparent;
}

.main-nav__link {
  position: relative;
  padding: 15px 11px;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
}

.main-nav__link:hover {
  text-decoration: underline;
}

.hero {
  padding: 200px 0;
  color: #FFF;
  background-color: #181818;
  background-repeat: no-repeat;
  background-size: 500px;
  background-position: right center;
}

.hero__wrap {
  padding: 0 20px;
}

.logo {
  display: flex;
  flex: 0 0 auto;
}

.logo__img {
  display: block;
  width: 328.4px;
  height: 46px;
}

.btn {
  display: inline-block;
  padding: 18px 20px;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
  color: #fff;
  text-decoration: none;
  border-radius: 36px;
  background-color: #90c53c;
  user-select: none;
}

.btn:hover, .btn:focus {
  background-color: #6eab0f;
}

.btn:active {
  color: #eee;
}
